<script>
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useRouter } from 'vue-router'
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
import { getLang, setLang } from "@/utils/i18n";
import { Toast } from 'vant';
import Clipboard from 'clipboard'
import AccountToast from "@/components/AccountToast";
export default {
  components: {
    AccountToast
  },
  props: {
    isFold: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:isFold'],
  setup (props, {emit}) {
    const store = useStore()
    const { t } = useI18n()
    const router = useRouter()
    // const route = useRoute
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      open: false,
      location: window.location,
      fold: computed({
        get () {
          console.log(props.isFold)
          return props.isFold
        },
        set (value) {
          console.log(value)
          emit('update:isFold', value)
        }
      }),
      headerActive: 0,
      locale: getLang().toLowerCase() || "tw",
      showLang: false,
      isActive: false,
      langList: [
        { text: '繁體', lang: 'tw' },
        { text: 'English', lang: 'en' },
        { text: 'español', lang: 'es' },
        { text: 'Français', lang: 'fr' },
        { text: 'Italiano', lang: 'it' },
        { text: '日本', lang: 'ja' },
        { text: '한국인', lang: 'ko' }
      ],
      menuConfig: [
        {
          label: 'Home',
          icon: 'HomeIcon',
          href: '/#/',
        },
        // {
        //   label: 'Trade',
        //   icon: 'TradeIcon',
        //   fold: true,
        //   items: [
        //     {
        //       label: 'Exchange',
        //       href: 'https://sero.goer.cloud/swap/#/swap',
        //     },
        //     {
        //       label: 'Liquidity',
        //       href: 'https://sero.goer.cloud/swap/#/pool',
        //     }
        //   ],
        // },
        {
          label: 'My Created',
          icon: 'TradeIcon',
          href: '/#/createlist',
        },
      ],
      showAccountToast: false,
      ConnectWallet: () => {
        store.commit('updateShowWallets', true)
      },
      changeLanuage: (val) => {
        setLang(val)
        data.locale = val
        data.showLang = false
      },
      initContract: () => {
        data.initData()
      },
      initData: () => {
      },
      initWallet: () => {
        web3Data.initWeb3()
      },
      onMenu: () => {
        let flag = !data.open
        data.open = flag
        document.querySelector('body').style.overflow = flag ? 'hidden' : 'auto'
      },
      openWebview: (url) => {
        window.location.href = url
      },
      closeWeb: () => {
        router.replace({ name: 'Index' })
        document.querySelector('body').style.overflow = 'auto'
        data.open = false
      },
      closeMenu: () => {
        document.querySelector('body').style.overflow = 'auto'
        data.open = false
      },
      onSelectLang: (action) => {
      if (action.lang) {
        data.changeLanuage(action.lang)
      }
      data.showLang = false
    }
    })
    onMounted(() => {
      var clipboard = new Clipboard('.clip-menu-button')
      clipboard.on('success', function (e) {
        Toast('Copy success')
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast('Copy error')
      })
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
