<template>
  <div class="body-container">
    <HeadTop v-model:isFold="isFold" />
    <div class="app-content" :class="{'fold': isFold}">
      <div class="main-container">
        <div class="main-container--wrap">
          <SuccessTip />
          <router-view v-slot="{ Component }">
            <keep-alive>
              <component :is="Component" v-if="$route.meta.keepAlive" />
            </keep-alive>
            <component :is="Component" v-if="!$route.meta.keepAlive" />
          </router-view>
        </div>
      </div>
    </div>
    <wallet-model />
  </div>
</template>

<script>
import HeadTop from '@/components/HeadTop'
import SuccessTip from "@/components/SuccessTip";
import { onMounted, ref, watch } from "vue";
import web3Data from "@/utils/initWeb3";
import WalletModel from "@/components/WalletModel";
export default {
  components: {
    HeadTop,
    WalletModel,
    SuccessTip
  },
  name: 'App',
  setup () {
    let isFold;
    let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
    if(flag) {
      isFold = ref(true)
    } else {
      isFold = ref(true)
    }
     
    
    onMounted(async () => {
     await web3Data.initWeb3()
    })
    watch(() => isFold, () => {
      console.log(isFold)
    })
    // 禁止ios10 以上页面缩放
    document.documentElement.addEventListener('touchstart', function (event) {
      if (event.touches.length > 1) {
        event.preventDefault()
      }
    }, false)
    let lastTouchEnd = 0
    document.addEventListener('touchend', function (event) {
      let now = Date.now()
      if (now - lastTouchEnd <= 300) {
        event.preventDefault()
      }
      lastTouchEnd = now
    }, false)
    // 阻止双指放大
    document.addEventListener('gesturestart', function (event) {
      event.preventDefault()
    })
    return {
      isFold
    }
  }
}
</script>

<style lang="less">
@import './assets/style/common.less';
.main-container--wrap {
  margin: 0 auto;
  padding: 0 0 1px;
  max-width: 436px;
  width: 100%;
}
.app-content {
  margin-top: 64px;
  -webkit-transition: margin-top 0.2s,margin-left 0.2s cubic-bezier(0.4,0,0.2,1);
  transition: margin-top 0.2s,margin-left 0.2s cubic-bezier(0.4,0,0.2,1);
  max-width: 100%;
}
.main-container {
  padding: 32px 16px;
}
@media screen and (min-width: 968px) {
  .app-content {
    margin-left: 240px;
    max-width: calc(100% - 240px);
    &.fold {
      margin-left: 56px;
      max-width: calc(100% - 56px);
    }
  }
  .main-container {
    background-image: url('~@/assets/images/arch-light.svg');
    background-repeat: no-repeat;
    background-position: center 420px,10% 230px,90% 230px;
    background-size: contain,266px,266px;
    min-height: 90vh;
  }
}
</style>
