<template>
  <div v-show="isShow" class="toast-wrapper flex-center">
    <div class="toast-mask" @click="isShow = false"></div>
    <div class="toast-box">
      <div class="toast-title flex-middle">
        <span class="flex-item">{{$t('index.Your wallet')}}</span>
        <div class="flex-center close-btn" @click="isShow = false">
          <svg viewBox="0 0 24 24" color="primary" width="20px" xmlns="http://www.w3.org/2000/svg" class="sc-bdfBwQ gzLedg"><path d="M18.3 5.70997C17.91 5.31997 17.28 5.31997 16.89 5.70997L12 10.59L7.10997 5.69997C6.71997 5.30997 6.08997 5.30997 5.69997 5.69997C5.30997 6.08997 5.30997 6.71997 5.69997 7.10997L10.59 12L5.69997 16.89C5.30997 17.28 5.30997 17.91 5.69997 18.3C6.08997 18.69 6.71997 18.69 7.10997 18.3L12 13.41L16.89 18.3C17.28 18.69 17.91 18.69 18.3 18.3C18.69 17.91 18.69 17.28 18.3 16.89L13.41 12L18.3 7.10997C18.68 6.72997 18.68 6.08997 18.3 5.70997Z"></path></svg>
        </div>
      </div>
      <div class="toast-content">
        <div class="addr" ref="accAddr">{{address}}</div>
        <div class="btns flex-middle">
          <a :href="bscLink" target="_blank" class="flex-middle">
            <span>{{$t('index.View on BscScan')}}</span>
            <svg viewBox="0 0 24 24" color="primary" width="20px" xmlns="http://www.w3.org/2000/svg" class="sc-bdfBwQ hRIkHd"><path d="M18 19H6C5.45 19 5 18.55 5 18V6C5 5.45 5.45 5 6 5H11C11.55 5 12 4.55 12 4C12 3.45 11.55 3 11 3H5C3.89 3 3 3.9 3 5V19C3 20.1 3.9 21 5 21H19C20.1 21 21 20.1 21 19V13C21 12.45 20.55 12 20 12C19.45 12 19 12.45 19 13V18C19 18.55 18.55 19 18 19ZM14 4C14 4.55 14.45 5 15 5H17.59L8.46 14.13C8.07 14.52 8.07 15.15 8.46 15.54C8.85 15.93 9.48 15.93 9.87 15.54L19 6.41V9C19 9.55 19.45 10 20 10C20.55 10 21 9.55 21 9V4C21 3.45 20.55 3 20 3H15C14.45 3 14 3.45 14 4Z"></path></svg>
          </a>
          <span @click="cpAddr" class="flex-middle">
            <span>
              {{$t('index.Copy Address')}}
            </span>
            <svg viewBox="0 0 24 24" width="20px" color="primary" xmlns="http://www.w3.org/2000/svg" class="sc-bdfBwQ hRIkHd"><path d="M15 1H4C2.9 1 2 1.9 2 3V16C2 16.55 2.45 17 3 17C3.55 17 4 16.55 4 16V4C4 3.45 4.45 3 5 3H15C15.55 3 16 2.55 16 2C16 1.45 15.55 1 15 1ZM19 5H8C6.9 5 6 5.9 6 7V21C6 22.1 6.9 23 8 23H19C20.1 23 21 22.1 21 21V7C21 5.9 20.1 5 19 5ZM18 21H9C8.45 21 8 20.55 8 20V8C8 7.45 8.45 7 9 7H18C18.55 7 19 7.45 19 8V20C19 20.55 18.55 21 18 21Z"></path></svg>
          </span>
        </div>
        <div class="flex-center">
          <div class="logout-btn" @click="logout">{{$t('index.Logout')}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {  reactive, toRefs, computed, watch } from "vue";
import { connectorId } from "@/storage"
import { Toast } from 'vant';
import { useStore } from "vuex";

export default {
  props: {
    showAccountToast: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:showAccountToast'],
  setup(props, {emit}) {
    const store = useStore()
    const data = reactive({
      isShow: computed({
        get () {
          console.log(props.showAccountToast)
          return props.showAccountToast
        },
        set (value) {
          console.log(value)
          emit('update:showAccountToast', value)
        }
      }),
      address: computed(() => store.state.accounts[0]),
      netId: computed(() => store.state.netId),
      bscLink: '',
      initData: () => {
        console.log(data.netId)
        data.bscLink = Number(data.netId) === 97 ? `https://testnet.bscscan.com/address/${data.address}` : `https://bscscan.com/address/${data.address}`
      },
      logout () {
        data.isShow = false
        connectorId.remove()
        store.commit("updateAccount", [])
      },
    })
    if (data.address) {
      data.initData()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initData()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  },
  methods: {
    
    cpAddr () {
      console.log('lll');
      var copyDOM = this.$refs.accAddr
      var range = document.createRange()
      window.getSelection().removeAllRanges()
      range.selectNode(copyDOM)
      window.getSelection().addRange(range)
      var successful = document.execCommand('copy')
      if(successful){
        Toast('Copied')
      }else{
        Toast('Copy failed')
      }
      window.getSelection().removeAllRanges()
    }
  }
}
</script>
<style lang="less" scoped>
#app {
  .toast-wrapper {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1001;
  }
  .toast-mask {
    position: fixed;
    z-index: 10;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #452a7a;
    opacity: 0.6;
  }
  .toast-box {
    overflow: hidden;
    background: #FFFFFF;
    box-shadow: 0px 20px 36px -8px rgb(14 14 44 / 10%), 0px 1px 1px rgb(0 0 0 / 5%);
    border: 1px solid #E9EAEB;
    border-radius: 32px;
    width: 100%;
    z-index: 100;
    .toast-title {
      padding: 12px 24px;
      border-bottom: 1px solid #E9EAEB;
      color: #452A7A;
      font-size: 20px;
      font-weight: 600;
      line-height: 1.1;
      .close-btn {
        width: 48px;
        height: 48px;
        svg {
          fill: #1FC7D4;
        }
      }
    }
    .toast-content {
      padding: 24px;
      .addr {
        color: #452A7A;
        font-size: 20px;
        font-weight: 600;
        line-height: 1.5;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 8px;
      }
      .btns {
        margin-bottom: 32px;
        > span, > a {
          font-size: 14px;
          font-weight: 600;
          line-height: 1.5;
          color: #1FC7D4;
          margin-right: 16px;
          cursor: pointer;
          svg {
            margin-left: 4px;
            fill: #1FC7D4;
          }
        }
        a:hover {
          -webkit-text-decoration: underline;
          text-decoration: underline;
        }
      }
      .logout-btn {
        height: 32px;
        line-height: 28px;
        border-radius: 16px;
        cursor: pointer;
        font-size: 16px;
        font-weight: 600;
        letter-spacing: 0.03em;
        padding: 0px 16px;
        border: 2px solid #1FC7D4;
        color: #1FC7D4;
        &:hover {
          opacity: 0.8;
        }
      }
    }
  }
  @media screen and (min-width: 370px) {
    .toast-box {
      width: auto;
      min-width: 320px;
      max-width: 100%;
    }
  }
}
</style>
