<template>
<div class="success-tip" v-if="showDialog && result.time > new Date().getTime() - 3000">
  <div class="success-box flex-middle">
      <div class="svg-box">
         <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="#31D0AA" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><path d="M22 11.08V12a10 10 0 1 1-5.93-9.14"></path><polyline points="22 4 12 14.01 9 11.01"></polyline></svg>
      </div>
      <div class="content flex-item">
         <div class="title">{{result.desc}}</div>
         <div class="link" @click="toUrl(result.hash)">{{$t('index.View on bscscan')}}</div>
      </div>
      <div class="close-box" @click="showDialog = false">
         <svg viewBox="0 0 24 24" class="primary" color="primary" width="20px" xmlns="http://www.w3.org/2000/svg"><path d="M18.3 5.70997C17.91 5.31997 17.28 5.31997 16.89 5.70997L12 10.59L7.10997 5.69997C6.71997 5.30997 6.08997 5.30997 5.69997 5.69997C5.30997 6.08997 5.30997 6.71997 5.69997 7.10997L10.59 12L5.69997 16.89C5.30997 17.28 5.30997 17.91 5.69997 18.3C6.08997 18.69 6.71997 18.69 7.10997 18.3L12 13.41L16.89 18.3C17.28 18.69 17.91 18.69 18.3 18.3C18.69 17.91 18.69 17.28 18.3 16.89L13.41 12L18.3 7.10997C18.68 6.72997 18.68 6.08997 18.3 5.70997Z"></path></svg>
      </div>
    </div>
</div>
</template>

<script>
import config from "@/utils/config";
import { reactive, toRefs, computed } from "vue";
import { useStore } from "vuex";
export default {
  setup () {
    const store = useStore()
    const data = reactive({
       result: computed(() => store.state.result),
       showDialog: computed({
        get () {
          if(!store.state.result) {
            return false
          }
          return store.state.result.isShow
        },
        set (value) {
           store.commit('updateResult', {
               hash: data.result.hash,
               time: data.result.time,
               desc: 'Create project',
               isShow: value
             })
        }
      }),
      toUrl: (hash) => {
        window.location.href = config.explore+ '/tx/' + hash
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.success-tip{
  padding: 0px 0px 15px;
}
.primary{
  fill: rgb(31, 199, 212);
}
.success-box{
  background: #fff;
  border-radius: 16px;
  padding: 15px;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.1);
  .title{
     color: var(--primary-color);
     font-size: 15px;
     font-weight: 600;
  }
  .link{
    color: rgb(31, 199, 212);
  }
  .svg-box{
    margin-right: 10px;
  }
}
</style>
