import { createStore } from "vuex";
import { netId,  transactions} from "@/storage"
export default createStore({
  state: {
    accounts: [],
    netId: netId.get() || '0',
    provider: null,
    currentBalance: "",
    locale: '',
    initem: '',
    price: '',
    chartData: [],
    showWallets: false,
    transactions: JSON.parse(transactions.get()) || {},
    result: null
  },
  mutations: {
    updateAccount(state, value) {
      state.accounts = value;
    },
    updateProvider(state, value) {
      state.provider = value;
    },
    updateCurrentBalance(state, value) {
      state.currentBalance = value;
    },
    updateLocale(state, value) {
      state.locale = value;
    },
    updateNetId(state, value) {
      state.netId = value;
      netId.set(value)
    },
    updatePrice(state, value) {
      state.price = value;
    },
    initChartData(state, value) {
      state.chartData = [...value, ...state.chartData];
    },
    updateChartData(state, value) {
      state.chartData.push(value)
    },
    updateInitem(state, value) {
      state.initem = (value)
    },
    updateShowWallets(state, value) {
      state.showWallets = value;
    },
    updateTransactions(state, value) {
      state.transactions[netId.get()][value.hash] = value;
      transactions.set(JSON.stringify(state.transactions))
    },
    updateResult(state, value) {
      state.result = value
    }
  },
  actions: {},
  modules: {}
});
