<script>
import { computed, reactive, toRefs } from "vue";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
export default {
  data () {
    return {
    }
  },
  setup () {
    const store = useStore()
    const data = reactive({
      showWallets: computed(() => store.state.showWallets),
      initWallet: () => {
        web3Data.initWeb3()
        data.closeWallets()
      },    
      closeWallets () {
        store.commit('updateShowWallets', false)
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
#app {
